.kg-width-wide {
  @extend .width-large;
}

.kg-width-full {
  @extend .width-ex-large;
}

.kg-image-card,
.kg-gallery-card {
  margin: 0 0 1.5em;
}

.kg-image-card figcaption {
  .kg-image-card figcaption,
  .kg-gallery-card figcaption {
    margin: -1em 0 1.5em;
    color: $post-figcaption-color;
  }
}

.kg-gallery-container {
  display: flex;
  flex-direction: column;
  margin: 1.5em auto;
  max-width: 1040px;
  width: 100vw;
}

.kg-gallery-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.kg-gallery-image img {
  display: block;
  margin: 0;
  width: 100%;
  height: 100%;
}

.kg-gallery-row:not(:first-of-type) {
  margin: 0.75em 0 0 0;
}

.kg-gallery-image:not(:first-of-type) {
  margin: 0 0 0 0.75em;
}

.kg-bookmark-publisher {
  margin: 0 0.5em;
}
